import { observable, computed } from "mobx"
import { onError } from "mobx-react"

class Store {
    @observable products = [];
    @observable cart = [];

    @computed get allProducts() {
        return this.products;
    }

    @computed get productsInCart() {
        return this.cart;
    }

    @computed get cartAmount() {
        const amount = this.cart
            .map(item => item.quantity)
            .reduce(prev, curr => prev + curr)

        return amount
    }

    setProducts(array) {
        this.products = array || [];
    }

    checkIfItemExists() {

    }

    updateOrder() {

    }

    persistCart(cart) {
        this.cart = cart;
    }

    // add a single obj
    addToCart(item, quantity) {
        this.cart.push({
            id: item.id,
            quantity
        })

    }
}

// log mobx errors
onError(error => {
    console.log(error)
});

// export singleton store
export const store = new Store();
